.NoteContainer {
	display: flex;
	flex-direction: column;
	width: 520px;
	margin-top: 20px;
	transition: 0.2s;
}

.NoteContainer:hover {
	transform: scale(1.05);
}

.Note {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-inline: 20px;
}

.NoteLeft {
	display: flex;
	align-items: center;
	gap: 20px;
}

.CustomCheckbox {
	height: 20px;
	width: 20px;
	border-style: solid;
	border-width: 1px;
	border-color: #6c63ff;
	border-radius: 5px;

	cursor: pointer;
}

.Checked {
	background-color: #6c63ff;
}

.textChecked {
	text-decoration: line-through;
}

.CustomCheck {
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 17px;
	width: 100%;
	height: 100%;
	transition: 0.1s;
}

.CustomCheckbox:hover {
	transform: scale(1.1);
}

.NoteTools {
	display: flex;
	gap: 10px;
	font-size: 20px;
}

.NoteEdit,
.NoteDelete {
	cursor: pointer;
	transition: 0.1s;
}

.NoteEdit:hover,
.NoteDelete:hover {
	transform: scale(1.5);
}

.NoteUnderline {
	width: 100%;
	background-color: #6c63ff;
	height: 1px;
	opacity: 0.5;
}

.EditInput {
	border: none;
	outline: none;
	background-color: inherit;
	color: inherit;
	font-size: inherit;
	margin-block: 1em;
	padding: 0;
}

.IsEdit {
	border-color: red;
	background-color: red;
}